<template>
  <div id="updatePwd">
    <div id="formWrap">
    	<div class="inputWrap">
    		<h1>修改密码</h1>
    	</div>
    	<hr>
    	<div class='inputWrap' style='marginTop:20px'>
    		<span class='label ' >&#12288原密码：</span>
    		<input type="text" v-model = 'prepassword'>
    	</div>
    	<div class='inputWrap'>
    		<span class='label' >&#12288新密码：</span>
    		<input type="password" v-model = 'password'>
    	</div>
    	<div class='inputWrap'>
    		<span class='label' >确认密码：</span>
    		<input type="password" v-model = 'repassword'>
    	</div>
    	<button id='submit' @click = 'updatePwd'>确认修改</button>
        <p id = 'errMessages' style='color:red;text-align:center;'></p>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import axios from 'axios'
export default {
  name: 'updatePwd',
  computed:{
    ...mapGetters([
      'user'
    ])
  },
  data(){
    return {
        isAll:false,
        prepassword: '',
        password : '',
        repassword: ''
    }
  },
  methods:{
     updatePwd(){
        // alert(this.prepassword + ' '+this.password +' '+this.repassword)
       if(this.prepassword=='' || this.password=='' || this.repassword==''){
           // $('#errMessage').html('请填写完整的信息！');
           document.getElementById('errMessages').innerHTML = '请填写完整的信息！';
           this.isAll = false;
        }else if(this.prepassword != '' && this.password != '' && this.repassword != ''){
            if(this.prepassword != this.user.password){
                document.getElementById('errMessages').innerHTML = '密码错误！';
                this.isAll = false;
            }else{
               if(this.password == this.repassword){
                    const local = '/sys/user';
                    const urls = local + '/modify_password';
                    let params = new URLSearchParams();
                    params.set('jobNumber',Number(this.user.jobNumber))
                    password.set('password',this.password)
                    axios({
                        method:'put',
                        url:urls,
                        data:params
                    })
                    .then(function (response) {
                        if(response.data.code == 0){
                            document.getElementById('errMessages').innerHTML = '修改密码成功！';
                        }
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
                     document.getElementById('errMessages').innerHTML = '';
                }else if(this.password != this.repassword){
                    document.getElementById('errMessages').innerHTML = '两次密码不一致，清重新输入！';
                    this.password = '';
                    this.repassword = '';
                }
            }
        }
     }
  },
  watch:{
    isAll(){
        if(this.isAll){
            document.getElementById('errMessage').innerHTML = '';
        }
    }
  }
}
</script>

<style scoped lang='stylus'>
 #updatePwd
 	width: 900px;
 	height:500px;
 	background-color:white;
 	margin:10px auto;
 	border:1px solid white;
 	display:flex;
 	flex-direction: row;
 	align-items: center;
 	justify-content: center;
 	#formWrap
 		width: 500px;
 		height:300px;
 		border:1px solid #E2F7F3;
 		border-radius:5px;
 		background-color:#E2F7F3;             
 		.inputWrap
 			width: 300px;
 			height:50px;
 			margin:0px auto;
 			display:flex;
 			align-items: center;
 			justify-content: center;
 		    .label
 			    color:black;          
 		#submit
 			clear:both;
 			width:150px;
 			height:25px;
 			margin-top:15px;
 			margin-left:175px;
 			background-color:#82F4B9;
 			border:0;
 			border-radius:5px;
 			&:hover
 				transform:scale(1.2,1.2);
 				transition:all 0.5s;
 				cursor: pointer;          
 	                  
 			
 				
</style>



