import * as type from './mutationType'

const mutations = {
	[type.SET_USER](state,user){
		state.user = user;
	},
	[type.SET_COURSE](state,course){
		state.course = course;
	},
	[type.SET_COUNT](state,count){
		state.count = count;
	},
	[type.SET_LOGOFTUNE](state,log){
		state.logOfTune = log;
	},
	[type.SET_CURRENTCOUR](state,currentCour){
		state.currentCour = currentCour;
	},
	[type.SET_SHOW](state,show){
		state.show = show;
	}
}

export default mutations


