<template>
  <div id="addUser">
    <div id="form">
    	<span id="title">新增用户</span>
    	<hr>
    	<div class="input first">
    		<span>&#12288账&#12288号：</span>
    		<input type="text" v-model = "jobnumber">
    	</div>
      <div class="input">
        <span>&#12288姓&#12288名：</span>
        <input type="text" v-model = 'name'>
      </div>
    	<div class="input">
    		<span>&#12288密&#12288码：</span>
    		<input type="password" v-model = 'password'>
    	</div>
    	<div class="input">
    		<span>确认密码：</span>
    		<input type="password" v-model = 'repassword'>
    	</div>
    	<div class="input">
    		<button id="submit" @click = 'adduser'>确认提交</button>
    	</div>
    	<p id = 'errMessage'></p>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'addUser',
  data(){
  	return {
  		isAll:false,
      isExit:false,
  		jobnumber:'',
      name:'',
  		password:'',
  		repassword:''
  	}
  },
  methods:{
  	adduser(){
  		// alert(this.password);
  		if(this.name =='' || this.jobnumber=='' || this.password=='' || this.repassword==''){
           // $('#errMessage').html('请填写完整的信息！');
           document.getElementById('errMessage').innerHTML = '请填写完整的信息！';
           this.isAll = false;
  		}else if(this.name !='' && this.jobnumber != '' && this.password != '' && this.repassword != ''){
  			if(this.password == this.repassword){
  				this.isAll = true;
  				const local = '/sys/user';
  				const urls = local + '/add';
          let params = new URLSearchParams();
          params.set('jobNumber',Number(this.jobNumber))
          params.set('password',this.password)
          params.set('username',this.name)
          params.set('college',null)
          params.set('department',null)
          params.set('role',1)
  				axios({
  					method:'post',
  					url:urls,
  					data:params
  				})
  				.then(function (response) {
  					if(response.data.code == 0){
              document.getElementById('errMessage').innerHTML = '添加用户成功！';
            }else{
              document.getElementById('errMessage').innerHTML = '添加用户失败！';
            }
  				})
  				.catch(function (error) {
  					console.log(error);
  				});
  			}else if(this.password != this.repassword){
             document.getElementById('errMessage').innerHTML = '两次密码不一致，清重新输入！';
  			    this.password = '';
  			    this.repassword = '';
  			}
  		}
  	}
  },
  watch:{
  	isAll(){
  		if(this.isAll){
  			document.getElementById('errMessage').innerHTML = '';
  		}
  	}
  }
}
</script>

<style>
 #addUser{
 	width: 900px;
 	height: 500px;
 	margin-top:10px;
 	border:1px solid white;
 	background-color: white;
 	display: flex;
 	justify-content: center;
 	align-items: center;
 }
#form{
	width: 500px;
 	height:310px;
 	border:1px solid #E2F7F3;
 	border-radius:5px;
 	background-color:#E2F7F3;
}
#title{
	color:#4DBEF2;
	display: block;
	font-size:15px;
	width:100px;
	font-weight: bold;
	margin-left:12.5px;
	margin-top: 15px;
	margin-bottom: 5px;
}
hr{
	width: 95%;
	color:red;
	margin:0 auto;

}	
.input{
	height: 40px;
	width: 70%;
	margin: 0px auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.first{
	margin-top:20px; 
}
#submit{
	clear:both;
	margin: 0 auto;
	border-radius: 3px;
	background-color: #39BAF5;
	height: 25px;
	width: 130px;
	margin-top: 25px;
	border:0;
}
#submit:hover{
	transform: scale(1.2,1.2);
	cursor: pointer;
	transition: all 0.3s;
}
#errMessage{
	/*border: 1px solid red;*/
	text-align: center;
	color:red;
	margin:15px auto;
}
</style>



