<template>
	<div>
		<div id="conSidebar">
			<div class='ulWrap'>
				<span>调课相关</span>
				<ul>
					<li >
						<router-link to="Tune">申请调课</router-link>
					</li>
					<li @click = 'teaLogOfTune'>
						<router-link to="logOfTune">调课记录</router-link>
					</li>
				</ul>
			</div>
			<div class='ulWrap'>
				<span>账号管理</span>
				<ul>
					<li >
						<router-link to="updatePwd">修改密码</router-link>
					</li>
				</ul>
			</div>
			<div class='ulWrap'>
				<span>用户管理</span>
				<ul>
					<li >
						<router-link to="addUser">新增用户</router-link>
					</li>
					<li >
						<router-link to="delUser">删除用户</router-link>
					</li>
					<li>
						<router-link to="seeUser">查看用户</router-link>
					</li>
				</ul>
			</div>
			<div class='ulWrap'>
				<span>审核相关</span>
				<ul>
					<li @click = 'logOfTune_is_checked'>
						<router-link to="checked">已审核</router-link>
					</li>
					<li @click = 'logOfTuneList'>
						<router-link to="waitCheck">待审核</router-link>
					</li>
				</ul>
			</div>
			<div class='ulWrap'>
				<span>课表相关</span>
				<ul>
					<li >
						<router-link to="upload">上传课表</router-link>
					</li>
				</ul>
			</div>
		</div>
		<div id='conCenter'>
		  <keepalive>
			<router-view/>
		  </keepalive>
		</div>
	</div>
</template>

<script>
// import Layout from '@/components/layout
import { mapMutations,mapGetters } from 'vuex'
import axios from 'axios'
export default {
  name: 'index',
  computed:{
    ...mapGetters([
       'user'
    ])
  },
  data(){
  	return {
  	}
  },
  methods:{
  	...mapMutations([
      'SET_LOGOFTUNE'
  	]),
  	teaLogOfTune(){
  		let this_ = this
  		axios.get('/sys/application/list_by_user',{
  			params:{
  				jobNumber:Number(this.user.jobNumber)
  			}
  		}).then(function(res){
  			if(res.data.code == 0){
              this_.SET_LOGOFTUNE(res.data.data);
  			}
  		}).catch(function(err){
  			console.log(err);
  		})
  	},
  	logOfTune_is_checked(){
  		let this_ = this
  		axios.get('/sys/application/list_is_checked'
  		).then(function(res){
  			if(res.data.code == 0){
              this_.SET_LOGOFTUNE(res.data.data);
  			}
  		}).catch(function(err){
  			console.log(err);
  		})
  	},
  	logOfTuneList(){
  		let this_ = this
  		axios.get('/sys/application/list',{
  			params:{
  				status:1
  			}
  		}
  		).then(function(res){
  			if(res.data.code == 0){
               this_.SET_LOGOFTUNE(res.data.data);
  			}
  		}).catch(function(err){
  			console.log(err);
  		})
  	}
  }
}
</script>

<style  scoped lang='stylus'>
 #conSidebar
 	width:200px
 	// border:1px solid red
 	float:left
 	a
 		text-decoration:none
 		color:#282828
 	li:hover
 		background-color:#3DA8B5
 		cursor: pointer
 	.ulWrap
 		margin:10px auto
 		width: 180px
 		background-color:white
 		border:1px solid white
 	    
 		span
 			display:block
 			width: 175px
 			margin-top:5px
 			background-color:#E8E8E8
 			padding-left:8px
 			color:#78130E
 			font-weight:bold
 	    ul
 		    width: 160px
 		    background-color:white
 		    margin:0px auto
 		    margin-top:10px
 	    li
 		    list-style-type: none
 		    font-size:16px
 		    width:140px
 		    margin:10px auto
 		    text-align:center
 		    height:25px
 		    line-height:25px
 		    border:1px solid #c3c3c3
 		    boder:0
 		    border-radius:2px
 		    color:black
 #conCenter
 	// border:1px solid red
 	float:left
 	width:980px
 	margin-left:10px
</style>



