<template>
  <div id="seeUser">
    <ul id="ulWrap">
    	<li class='liTitle'>
    	   <span class='itemLi'>账&#12288号</span>
    	   <span class='itemLi'>姓&#12288名</span>
    	   <span class='itemLi'>密&#12288码</span>
        </li>
    	<li class='litext' v-for = '(item,index) in count'>
    	   <span class='itemLiText' :class="{libg:index %2 !=0 }">{{item.jobNumber}}</span>
    	   <span class='itemLiText' :class="{libg:index %2 ==0 }">{{item.username}}</span>
    	   <span class='itemLiText' :class="{libg:index %2 !=0 }">{{item.password}}</span>
    	</li>
    </ul>
  </div>
</template>

<script>

import axios from 'axios'
import { mapMutations, mapGetters} from 'vuex'
export default {
  name: 'seeUser',
  computed:{
    ...mapGetters([
      'count'  
    ])
  }
  ,
  data(){
    return {
      // count:[]
    }
  },
  methods:{
    ...mapMutations(
      [
        'SET_COUNT'
      ]
    ),
    getCountList(){
      const local = '/sys/user';
      let this_ = this
      axios.get(local + '/list').then(function(response){
        if(response.data.code == 0){
         this_.SET_COUNT ( response.data.data );
        }
      }).catch(function(err){
        console.log(err)
      })
    }
  
  },
  created(){
    this.getCountList()
    // this.SET_COUNT([
    //                     {id:'152052', pwd:'1235', status:'使用中'},
    //                     {id:'158945', pwd:'1235', status:'停用'}
    //                 ])
  }
}
</script>

<style scoped lang='stylus'>

#seeUser
 	width: 900px;
 	height: 500px;
 	margin-top:10px;
 	border:1px solid white;
 	background-color: white;
 	display: flex;
 	justify-content: center;
 	align-items: flex-start;
 	#ulWrap
 		width:600px;
 		// border:1px solid red;
 		margin-top:50px;
 		.liTitle
 			list-style-type: none;
 			height:40px;
 			.itemLi
	 			width:32%;
	 			height:40px;
	 			display:block;
	 			float:left;
	 			color:white;
	 			font-size:20px;
	 			line-height:40px;
	 			border:1px solid #c3c3c3;
	 			text-align:center;
	 			background-color:#121111;
 		.litext
 			list-style-type: none;
 			height:25px;
 			.itemLiText
 				width:32%;
	 			height:25px;
	 			display:block;
	 			float:left;
	 			font-size:16px;
	 			line-height:25px;
	 			border:1px solid #c3c3c3;
	 			text-align:center; 
	 		.libg
	 			background-color:#DBDBDB;
	 		
				 	
</style>



