import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import ApplyTuneCour from '@/components/ApplyTuneCour'
import Hello from '@/components/Hello'
import logOfTune from '@/components/logOfTune'
import updatePwd from '@/components/updatePwd'
import addUser from '@/components/addUser'
import delUser from '@/components/delUser'
import waitCheck from '@/components/waitCheck'
import seeUser from '@/components/seeUser'
import upload from '@/components/upload'
import layout from '@/components/layout'
import login from '@/pages/login'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      component:login
    },
    {
      path: '/layout',
      name: 'layout',
      component: layout,
      children:[
            {
               path:'/',
               component:Index,
               children:[
                            {
                              path:'/',
                              component:Hello
                            },
                            {
                              path:'/Tune',
                              component:ApplyTuneCour
                            },
                            {
                              path:'/logOfTune',
                              component:logOfTune
                            },
                            {
                              path:'/updatePwd',
                              component:updatePwd
                            },
                            {
                              path:'/addUser',
                              component:addUser
                            },
                            {
                              path:'/delUser',
                              component:delUser
                            },
                            {
                              path:'/checked',
                              component:logOfTune
                            },
                            {
                              path:'/waitCheck',
                              component:waitCheck
                            },
                            {
                              path:'/seeUser',
                              component:seeUser
                            },
                            {
                              path:'/upload',
                              component:upload
                            }
                      ]
        },
        
      ]
    }
  ]
})



