<template>

    <router-view id = 'dis'></router-view>

</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'dis',
  computed:{
  	...mapGetters([
       'show'
  	])
  },
  methods:{
  	setBg(){
  		if(this.show == true){
  			document.body.style.backgroundImage = "url(../static/images/5.jpg)";
  		}else{
  			document.body.style.backgroundImage = "url(../static/images/1.jpg)";
  		}
  	}
  },
  watch:{
  	"$route":"setBg"
  },
  mounted(){
  	document.body.style.backgroundImage = "url(../static/images/5.jpg)";
  }
}
</script>

<style  lang='stylus'>
body
	width:100%;
	height:100%;
	// background-image:url(../assets/images/5.jpg);
	background-repeat:no-repeat;
	background-size:cover;
</style>



