<template>
  <div id="deluser">
    <ul id="ulWrap">
    	<li class='liTitle'>
    	   <span class='itemLi'>账&#12288号</span>
    	   <span class='itemLi'>姓&#12288名</span>
    	   <span class='itemLi'>密&#12288码</span>
    	   <span class='itemLi'>编&#12288辑</span>
        </li>
    	<li class='litext' v-for = '(item,index) in count'>
    	   <span class='itemLiText' :class="{libg:index %2 !=0 }">{{item.jobNumber}}</span>
    	   <span class='itemLiText' :class="{libg:index %2 ==0 }">{{item.username}}</span>
    	   <span class='itemLiText' :class="{libg:index %2 !=0 }">{{item.password}}</span>
    	   <span class='itemLiText' :class="{libg:index %2 ==0 }">
    	     <button class='del' @click='delCount(index)'>删除</button>
    	   </span>
    	</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
import { mapMutations, mapGetters} from 'vuex'
import $ from 'jquery'
export default {
  name: 'delUser',
  computed:{
    ...mapGetters([
      'count'  
    ])
  }
  ,
  data(){
    return {
      // count:[]
    }
  },
  methods:{
    ...mapMutations(
      [
        'SET_COUNT'
      ]
    ),
    getCountList(){
      const local = 'sys/user';
      let this_ = this
      axios.get(local + '/list').then(function(response){
        if(response.data.code == 0){
          this_.SET_COUNT ( response.data.data );
        }
      }).catch(function(err){
        console.log(err)
      })
    },
    delCount(index){
    	const local = 'sys/user';
        let val = $(".del:eq("+ index +")").parent().parent().find('span:eq(0)').html();
        let this_ = this
        let params = new URLSearchParams();
        params.set('jobNumber',Number(val))
        axios.post(local+'/remove', params
        	).then(function(response){
	        if(response.data.code == 0){
	          this_.SET_COUNT ( response.data.data );
	        }
	    }).catch(function(err){
	        console.log(err)
	    })
    }
  },
  created(){
    this.getCountList()
    // this.SET_COUNT([
    //                     {id:'152052', pwd:'1235', status:'使用中'},
    //                     {id:'158945', pwd:'1235', status:'停用'}
    //                 ])
  }
}
</script>

<style scoped lang='stylus'>
#deluser
 	width: 900px;
 	height: 500px;
 	margin-top:10px;
 	border:1px solid white;
 	background-color: white;
 	display: flex;
 	justify-content: center;
 	align-items: flex-start;
 	#ulWrap
 		width:700px;
 		// border:1px solid red;
 		margin-top:50px;
 		.liTitle
 			list-style-type: none;
 			height:40px;
 			.itemLi
	 			width:23%;
	 			height:40px;
	 			display:block;
	 			float:left;
	 			color:white;
	 			font-size:20px;
	 			line-height:40px;
	 			border:1px solid #c3c3c3;
	 			text-align:center;
	 			background-color:#121111;
 		.litext
 			list-style-type: none;
 			height:25px;
 			.itemLiText
 				width:23%;
	 			height:25px;
	 			display:block;
	 			float:left;
	 			font-size:16px;
	 			line-height:25px;
	 			border:1px solid #c3c3c3;
	 			text-align:center;
	 			padding:0; 
	 			.del
	 				background-color:#A60A0A;
	 				width:70px;
	 				border:0;
	 				border-radius:3px;
	 				height:20px;
	 				color:#F1F1F1;
	 				&:hover
	 					cursor: pointer;
	 					transform:scale(1.1,1.1);
	 					transition:all 0.3s;
	 					background-color:#510606;
	 					
	 		.libg
	 			background-color:#DBDBDB;	 			 
</style>



