<template>
  <div id="upload">
   <p id="errors"></p>
   <span id='select'>选择课表文件：</span>
   <input type="file" id="file" /><br />
   <button id="uploadBtn" @click = 'upload'>上传</button>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'upload',
  data(){
  	name:''
  },
  methods:{
  	upload()
		{
        
		 let filename = $('#file').val();
		 let flag = false; //状态
		 let arr = ["xls","xlsx"];
		 //取出上传文件的扩展名
		 let index = filename.lastIndexOf(".");
		 let ext = filename.substr(index+1);
		 //循环比较
		 for(let i=0;i<arr.length;i++)
		 {
		  if(ext == arr[i])
		  {
		   flag = true; //一旦找到合适的，立即退出循环
		   break;
		  }
		 }
		 //条件判断
		 if(flag)
		 {
		 	 $('#errors').html(' ');
		    let formData = new FormData();
            formData.append("file", document.getElementById("file").files[0]); 
            $.ajax({
                    url: "/sys/course/upload",
                    type: "POST",
                    data: formData,
                    /**
                    *必须false才会自动加上正确的Content-Type
                    */
                    contentType: false,
                    /**
                    * 必须false才会避开jQuery对 formdata 的默认处理
                    * XMLHttpRequest会对 formdata 进行正确的处理
                    */
                    processData: false,
                    success: function (data) {
                        if (data.code == 0) {
                           $('#errors').html('上传文件成功！')
                        }else{
                            $('#errors').html('上传文件失败！')
                        }
                       
                    },
                    error: function () {
                        $('#errors').html('上传文件失败！')
                    }
            })
		 }else
		 {
		   $('#errors').html('文件格式不合法! 文件后缀必须为 .xls 或 .xlsx !')
		 }
    }

  }
}
</script>

<style scoped lang='stylus'>
#upload
 	width: 900px;
 	height: 500px;
 	margin-top:10px;
 	border:1px solid white;
 	background-color: white;
 	display:flex;
 	justify-content:center;
 	align-items:center;
 	align-contants:center;
 	flex-direction: column;
 	#errors
 		color:red;
 		font-size:20px;
 	#select
 		display:block;
 		// border:1px solid red;
 		width:20%;
 		color:#730606;
 		font-weight:bold;
 		font-size:20px;
 	#file
 		// border:1px solid red;
 		margin-top:15px;
 		margin-left:230px;
 	#uploadBtn
 		width:130px;
 		height:25px;
 		background-color:#14BD92;
 		border:0;
 		border-radius:4px;
 		margin-top:20px;
 		&:hover
 			transform:scale(1.2,1.2);
 			cursor: pointer;
</style>



