<template>
  <div id="Tune">
    <ul id="TeaCourse">
        <li id='titleLi'>
            <span class='titleSpan'>编号</span>
            <span class='titleSpan'>工号</span>
            <span class='titleSpan'>课程名</span>
            <span class='titleSpan'>班级名</span>
            <span class='titleSpan'>地点</span>
            <span class='titleSpan'>周次</span>
            <span class='titleSpan'>时间</span>
            <span class='titleSpan'>节次</span>
            <span class='titleSpan'>编辑</span>
        </li>
        <li class='contentLi' v-for = '(item, index) in course'>
            <span class="contentSpan" >{{item.id}}</span>
            <span class="contentSpan" >{{user.jobNumber}}</span>
            <span class="contentSpan" >{{item.name}}</span>
            <span class="contentSpan" >{{item.schoolClass}}</span>
            <span class="contentSpan" >{{item.location}}</span>
            <span class="contentSpan" >{{item.week}}</span>
            <span class="contentSpan" >{{day[index]}}</span>
            <span class="contentSpan" >{{timetable[index]}}</span>
            <span class="contentSpan">
                <button class="tunecour"   @click='setCurrentCour(index)'>调课</button>
            </span>
        </li>
    </ul>
    <watchCour v-show = "show"  v-on:close="turn" :shows="show"></watchCour>
  </div>
</template>

<script>
import watchCour from './watchCour'
import { mapMutations, mapGetters } from 'vuex'
import $ from 'jquery'
import axios from 'axios'
export default {
  name: 'Tune',
  computed:{
    ...mapGetters([
        'user',
        'course',
        'currentCour'
    ])
  },
  data(){
    return {
        show:false,
        timetable:[],
        day:[]
        // course:[
        //     {id:1,jobNumber:15368,courName:'c++',class:'java159',location:'软件楼105教室大大苏打',week:17,day:'周三',timetable:'1 2节'},
        //     {id:2,jobNumber:15369,courName:'c++',class:'java158',location:'软件楼105教室大大苏打',week:17,day:'周三',timetable:'1 2节'}
        // ]
    }
  },
  components:{ watchCour },
  methods:{
    turn(){
        this.show=!this.show;
    },
    ...mapMutations([
        'SET_CURRENTCOUR',
        'SET_COURSE'
    ]),
    setCurrentCour(index){
        this.show = !this.show;
        let cour = {};
        let id = $('.tunecour:eq('+index+')').parent().parent().find('.contentSpan:eq(0)').html();
        let courName = $('.tunecour:eq('+index+')').parent().parent().find('.contentSpan:eq(2)').html();
        let className = $('.tunecour:eq('+index+')').parent().parent().find('.contentSpan:eq(3)').html();
        
        cour.id = id;
        cour.courName = courName;
        cour.class = className;

        this.SET_CURRENTCOUR(cour);
        // alert(this.currentCour.courName)
    },
    getCourList(){
        // alert('coming')
        let url = '/sys/course/list'
        let this_ = this
        axios.get(url,{
            params:{
                jobNumber:Number(this.user.jobNumber)
            }
        }).then(function(res){
           if(res.data.code == 0){
             this_.SET_COURSE(res.data.data);
           }
        }).catch(function(err){
            console.log(err)
        })
    },
    getTimetable(timeTable){
         let timetable = (timeTable).toString(2);
         let head = '';
            if(timetable.length<10){
                let add = 10 - timetable.length;
                for(let i = 0;i < add;i++){
                    head +='0';
                }
            }
          timetable = head+timetable;
          
          let left = 0;
          let right = 0;
          let middle = 0;
          left = timetable.indexOf('1');
          for(let i = left;i < timetable.length ; i++){
            if(timetable.charAt(i) == '1'){
                middle +=1;
            }
          }
          right = left + middle;
          let start = left + 1;

          return (start + '至 ' + right + '节')
    },
    setTimetable(arr){
        let timeTable=[];
        for(let i = 0;i<arr.length;i++){
          timeTable.push(this.getTimetable(arr[i].timetable))
        }
        return timeTable;
    },
    setDay(arr){
      let timeTable=[];
        for(let i = 0;i<arr.length;i++){
          if(arr[i].day == 1){
            timeTable.push('星期一');
          }else if(arr[i].day == 2){
             timeTable.push('星期二');
          }else if(arr[i].day == 3){
             timeTable.push('星期三');
          }else if(arr[i].day == 4){
             timeTable.push('星期四');
          }else if(arr[i].day == 5){
             timeTable.push('星期五');
          }else if(arr[i].day == 6){
             timeTable.push('星期六');
          }else{
             timeTable.push('星期日');
          }
        }
        return timeTable;
    }
  },
  created:function(){
    this.getCourList();
    this.timetable = this.setTimetable(this.course);
    this.day = this.setDay(this.course);
  }
}
</script>

<style>
 #Tune{
    width: 1050px;
 	height:500px;
 	background-color:white;
 	margin:10px auto;
 	border:1px solid white;
 }
 #TeaCourse{
    margin: 20px auto;
    width:1010px;
 }	    
 #titleLi{
    width: 100%;
 }
 #contentLi{
    width: 100%;
 }
 .titleSpan{
    display: block;
    float:left;
    width: 110px;
    border: 1px solid #c3c3c3;
    height: 35px;
    line-height: 35px;
    text-align: center;
    background-color: #050000;
    color:white;
 }
 .contentSpan{
    display:flex;
    float:left;
    width: 110px;
    border: 1px solid #c3c3c3;
    height: 50px;
    font-size: 12px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
 }
 .tunecour{
    width: 80px;
    height: 30px;
    background-color: #EB4747;
    border: 0;
    border-radius: 3px;
 }
 .tunecour:hover{
    transform: scale(1.2,1.2);
    cursor: pointer;
    transition: all 0.3s;
 }
 li{
    list-style-type:none; 
 }	
 				
 					
</style>



