<template>
  <div id="waitcheck">
   <div class='log' v-for = ' (item ,index) in logOfTune'>
   	 <p class="logsContent">{{item.content}}</p>
   	 <div id='btnWrap'>
   	   <button class='state' @click = 'pass(index)'>通过申请</button>
   	   <button class='state' @click = 'reject(index)'>拒绝申请</button>
   	 </div>
   	 <p class="err" style='color:red;'></p>
   </div>
  </div>

</template>

<script>
import { mapGetters } from 'vuex'
import $ from 'jquery'
import axios from 'axios'
export default {
  name: 'waitcheck',
  computed:{
    ...mapGetters([
      'logOfTune'
    ])
  },
  methods:{
    pass(index){
    	let params = new URLSearchParams();
    	params.set('id',Number(this.logOfTune[index].id))
    	params.set('status',0)
	  axios.put('/sys/application/modify',params
	  	).then(function(res){
	  		if(res.data.code == 0){
	            $('.err:eq('+index+')').html('操作成功！数据将在您退出此界面后更新！');
	  		}
	  	}).catch(function(err){
	  			console.log(err);
	  	})
  	},
  	reject(index){
  		let params = new URLSearchParams();
    	params.set('id',Number(this.logOfTune[index].id))
    	params.set('status',2)
	  axios.put('/sys/application/modify',params
	  	).then(function(res){
	  		if(res.data.code == 0){
	            $('.err:eq('+index+')').html('操作成功！数据将在您退出此界面后更新！');
	  		}
	  	}).catch(function(err){
	  			console.log(err);
	  	})
  	}

  }
}
</script>

<style  scoped lang='stylus'>
#waitcheck
 	width: 900px;
 	height: 500px;
 	margin-top:10px;
 	border:1px solid white;
 	background-color: white;
 	.log
	 	margin:3px auto;
	 	width:800px;
	 	height: 120px;
	 	background-color: #c3c3c3;
	 	display: flex;
	 	flex-direction: column;
	 	align-items: center;
	 	border-radius: 3px;
	 	justify-content: center;
	#btnWrap
		// border:1px solid red;
		margin:0;
		padding:0;
		margin-top:6px;
	.state
	 	background-color: #0DAF83;
	 	border-radius: 3px;
	 	border:0;
	 	margin:0;
 	.h
	 	height:20px;
	 	font-size:20px;
	 	margin:0;
	 	color:white;
	 	margin-top:10px;
	 	padding:0;
 	.tuneTo
 		color:#9E1010;
 	h3
	 	height: 15px;
	 	font-size:15px;
	 	margin-top:10px;
	 	color:white;
	 	padding:0;
	.state:hover
		cursor:pointer;
		transform:scale(1.2,1.2);
		background-color:#85EA88;
 	
</style>




