<template>
  <div id="layout">
    <div id="header">
    	<div id="headContent">
    		<router-link to='/layout'><img src="../assets/images/log.jpg" alt="" ></router-link>
    		<h2><router-link to='/layout'>南昌大学</router-link></h2>
    		<button @click = 'logout'>退出登录</button>
    		<span class='span'>您好！{{user.username}}老师</span>
    	</div>
    </div>
    <div id="content">
      <router-view></router-view>
    </div>
    <div id="footer">@NCU--南昌大学</div>
  </div>
</template>

<script>
import { mapGetters,mapMutations } from 'vuex'
export default {
  name: 'layout',
  computed:{
    ...mapGetters([
      'user',
      'show'
    ])
  },
  methods:{
    ...mapMutations([
      'SET_USER',
      'SET_SHOW'
    ]),
    setFooter(){
      let winHeight = window.screen.height; 
      let winWidth = window.screen.width;
      let bodyHeight = document.body.offsetHeight;
      let bodyWidth = document.body.offsetWidth;
      // alert(bodyWidth+bodyHeight+winHeight+winWidth);
      // alert(bodyHeight);
      let el = document.getElementById('footer');
      if(bodyHeight < winHeight){
        // alert((winHeight - bodyHeight) + 'px');
          el.style.marginTop = (winHeight) + 'px';
      }else{
          el.style.marginTop = 50 + 'px';
      }
    },
    logout(){
      this.SET_USER({});
      this.$router.push('/');
      this.SET_SHOW(true);
    }
  },
  created:function(){
    if(this.user.username == null){
      alert('请先登录！！我们将为您跳转到登录页面！')
      this.$router.push('/');
      this.SET_SHOW(true);
      return;
    }
  },
  mounted:function(){
    this.setFooter();
  } 
}
</script>

<style lang='stylus'>
  html,body, div, p, ul, li
 	  margin:0
 	  padding:0
  a
    text-decoration:none
    color:white
  body
 	  background-color:#ECECEC
    overflow:scroll;
  // #layout
  //   overflow:scroll;
  #header
 	  margin:0 
 	  background-color:#071065
 	  height:60px
 	  background-size:cover
    width:100%;
  #footer
    width:100%;
    background-color:#071065;
    height:60px;
    background-size:cover;
    display:flex;
    justify-content:center;
    align-items:center;
    color:white;
  #headContent
 	  margin:0 auto
 	  height:60px
 	  width:1200px
    // border:1px solid white
    img
      float:left
      width:55px
      height:55px	
      margin-top:2.5px
    .span
      float:right
      color:white
      line-height:60px
      margin-right:15px
     
    button
      float:right
      height:30px	
      margin-right:15px
      margin-top:15px
      border-radius:3px
      border:0
    button:hover
      background-color:#A30A0A
      cursor:pointer
    h2
      color:white
      font-size:20px
      margin-top:15px
      float:left
      margin-left:5px
  #content
 	  margin:10px auto
 	  width:1200px
    min-width:1200px   
</style>



