<template>
  <div id="watchCour" v-show='shows'>
    <div id="courWrap">
      <button id='close' @click='show1'></button>
      <div id="currCour">
        <h2 id="lab">您需要调课的课程是：</h2>
        <p id='courMess'>{{currentCour.class}}班&#12288的&#12288{{currentCour.courName}}&#12288课程</p>
      </div>
      <div id="tuneCourMess">
        <div id="err"></div>
        <div id="timeMess">
          <h3 id = 'time'>上课时间改至：</h3>
          <select name="week" id="week" v-model = 'week'>
            <option :value="item" v-for = 'item in 20' >第{{item}}周</option>
          </select>
          <select name="days" id="days" v-model = 'days'>
            <option value="星期一"  selected = 'selected'>星期一</option>
            <option value="星期二"  >星期二</option>
            <option value="星期三"  >星期三</option>
            <option value="星期四"  >星期四</option>
            <option value="星期五"  >星期五</option>
            <option value="星期六"  >星期六</option>
            <option value="星期日"  >星期日</option>
          </select>
          &#12288第&#12288
          <select name="from" id="from" v-model = 'froms'>
            <option :value="item" v-for='item in 10'>{{item}}</option>
          </select>
          &#12288到&#12288
          <select name="to" id="to" v-model = 'to'>
            <option :value="item" v-for='item in 10'>{{item}}</option>
          </select>&#12288节课
        </div>
        <div id="location">
          <h3 id="courLoca">上课地点改至：</h3>
          <input type="text" id = 'inputLoca' v-model = 'location'>
        </div>
        <button id="btnTune" @click = 'tuneCourse'>确认调课</button>
      </div>

    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
// import $ from 'jquery'
import axios from 'axios'
export default {
  name: 'watchCour',
  props:['shows'],
  computed:{
    ...mapGetters([
       'currentCour'
    ])
  },
  data(){
    return {
      week:1,
      days:'星期一',
      day:1,
      froms:1,
      to:1,
      location:''
    }
  },
  methods:{
    setSize:function(){
    	let heights = window.screen.height; 
    	let widths = window.screen.width;
    	let el = document.getElementById('watchCour');
    	el.style.height = heights +'px';
    	el.style.width = widths + 'px';
    },
    setPosition:function(){
          let bodyHeight = document.documentElement.clientHeight  ;
          let bodyWidth =document.documentElement.clientWidth
          let cour = document.getElementById('courWrap');
          if((bodyHeight - 625) / 2 <=0){
            cour.style.top=0+'px';
          }else{
            cour.style.top = (bodyHeight - 625) / 2 + 'px';
          }
          
          if((bodyWidth - 800) / 2 <= 0 ){
            cour.style.left = 0 + 'px';
          }else{
            cour.style.left = (bodyWidth - 800) / 2 + 'px';
          }
    },
    show1(){
      this.shows=!this.shows;
      this.$emit('close');
    },
    setDay(){
      if(this.days == '星期一'){
        this.day = 1;
      }else if(this.days == '星期二'){
        this.day = 2;
      }else if(this.days == '星期三'){
        this.day = 3;
      }else if(this.days == '星期四'){
        this.day = 4;
      }else if(this.days == '星期五'){
        this.day = 5;
      }else if(this.days == '星期六'){
        this.day = 6;
      }else{
        this.day = 7;
      }
    },
    setTimetable(){
        let timeTable;
        let left = this.froms - 1;
        let right = 10 - this.to;
        let middle = this.to - this.froms + 1;
        let leftString='';
        let rightString='';
        let middleString='';
        for(let i = 0; i  < left ;i ++){
          leftString += '0';
        }
        for(let i = 0; i  < middle ;i ++){
          middleString += '1';
        }
        for(let i = 0; i  < right ;i ++){
          rightString += '0';
        }
        timeTable = leftString+middleString+rightString ;
        return parseInt(timeTable,2);
    },
    tuneCourse(){
      // alert('coming...')
      this.setDay();
      this.setTimetable();
      if(this.to < this.froms){
         document.getElementById('err').innerHTML = '课程的开始节次不能大于结束节次！！';
         return;
       }else{
         document.getElementById('err').innerHTML = ''
         if(this.location == ''){
           document.getElementById('err').innerHTML = '上课地点不能为空！';
           return;
         }else{
           let params = new URLSearchParams();
           params.set('id',this.currentCour.id)
           params.set('location',this.location)
           params.set('week',this.week)
           params.set('day',this.day)
           params.set('timetable',this.setTimetable())
           axios({
            url:'/sys/course/apply',
            method:'post',
            data:params
           }).then(function(res){
              if(res.data.code == 0){
                document.getElementById('err').innerHTML = '调课申请已提交，等待管理员审核！！';
              }else{
                 document.getElementById('err').innerHTML = '调课申请提交失败！！';
              }
           }).catch(function(err){
              console.log(err);
           })
         }
       }
    }
  },
  mounted:function(){
        this.setSize();
        this.setPosition();
        window.onresize = function(){
          let bodyHeight = document.documentElement.clientHeight  ;
          let bodyWidth =document.documentElement.clientWidth
          let cour = document.getElementById('courWrap');
          if((bodyHeight - 625) / 2 <=0){
            cour.style.top=0+'px';
          }else{
            cour.style.top = (bodyHeight - 625) / 2 + 'px';
          }
          
          if((bodyWidth - 800) / 2 <= 0 ){
            cour.style.left = 0 + 'px';
          }else{
            cour.style.left = (bodyWidth - 800) / 2 + 'px';
          }
        };
  },
  watch:{
    to(){
       if(this.to < this.froms){
         document.getElementById('err').innerHTML = '课程的开始节次不能大于结束节次！！'
       }else{
         document.getElementById('err').innerHTML = ''
       }
    },
    froms(){
       if(this.to < this.froms){
         document.getElementById('err').innerHTML = '课程的开始节次不能大于结束节次！！'
       }else{
         document.getElementById('err').innerHTML = ''
       }
    }
  }
}
</script>

<style>
#watchCour{
  position: fixed;
  left:0;
  top:0;
  background-color:black;
  z-index:999;
  opacity:0.8;
  /*display:flex;
  justify-content:center;
  align-items:flex-start;*/
}


#courWrap{
  width: 800px;
  height: 625px;
  position: absolute;
  background-color: white;
  z-index: 9999;
  border-radius: 10px;
  box-shadow: 5px 5px 15px #A5A1E7;
  opacity: 1;
}
#currCour{
  position: absolute;
  width:600px;
  height:70px;
  background-color: #272727;
  color:white;
  left:100px;
  top:30px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  border-radius:15px;
  opacity: 1; 
}
#err{
 /* border:1px solid red;*/
 /* width:85%;*/
  height: 30px;
  font-size: 16px;
  color: red;
  text-align: center;
  font-weight: bold;
  margin:40px auto;
}
#tuneCourMess{
  position: absolute;
  width:600px;
  height:400px;
  background-color: #272727;
  color:white;
  left:100px;
  top:110px;
  /*display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  align-content: flex-start;*/
  border-radius:15px;
  opacity: 1; 
}
#timeMess{
  display: flex;
  align-items: center;
  align-content: center;
/*  border: 1px solid red;*/
  margin:0 auto;
  width:85%;
}
#location{
  display: flex;
  align-items: center;
  align-content: center;
 /* border: 1px solid red;*/
  width:85%;
  margin:0 auto;
}
#inputLoca{
  height: 25px;
  width: 200px;
}
.bg{
  background-color: #060309;
  color:white;
}
#btnTune{
  width:130px;
  height: 30px;
  border:0;
  border-radius: 5px;
  background-color: #E16565;
  margin-top: 40px;
  margin-left:235px;
}
#btnTune:hover{
  transform: scale(1.3,1.3);
  transition: all 0.4s;
  cursor: pointer;
  background-color:#7A0505;
}
#close{
  position: absolute;
  left:99%;
  top:-30px;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  font-size: 10px;
  border:0;
  background-color:#D54B4B;
  background-image: url(../assets/images/close.png);
  background-repeat: none;
  background-size: cover;
}
#close:hover{
  background-color:#7A0505;
  transform: scale(1.3,1.3);
  transition: all 0.4s;
  cursor: pointer;
}
</style>



