<template>
  <div id="logOfTune">
   <div class='logs' v-for = '(item ,index) in logOfTune'>
   	 <p class="logContent">{{item.content}}</p>
   	 <b class='state'>{{status[index]}}</b>
   </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'App',
  computed:{
    ...mapGetters([
      'logOfTune'
    ])
  },
  data(){
  	return {
      status:[]
  	}
  },
  methods:{
    setStatus(arr){
      let status=[];
      for(let i = 0;i<arr.length;i++){
        if(arr[i].status == 0){
          status.push('已通过审核')
        }else if(arr[i].status == 1){
          status.push('待审核')
        }else{
          status.push('审核不通过')
        }
      }
      return status;
    }
  },
  created:function(){
  	  this.status = this.setStatus(this.logOfTune);
  }
}
</script>

<style>
#logOfTune{
    width: 900px;
 	height:500px;
 	background-color:white;
 	margin:10px auto;
 	border:1px solid white;
 }
 .logs{
 	margin:3px auto;
 	width:800px;
 	height: 120px;
 	background-color: #c3c3c3;
 	display: flex;
 	flex-direction: column;
 	align-items: center;
  flex-wrap: wrap;
 	border-radius: 3px;
 	justify-content: center;
 }
 .logContent{
  font-size: 15px;
 }
 .state{
 	background-color: #0DAF83;
 	border-radius: 3px;
 	border:0;
  margin-top: 10px;
 }
</style>



