<template>
<div id="wraps" >
	<div id="login">
      <div id = 'head' class='comm'>
        <img src = "../assets/images/log.jpg">
        <h1>南昌大学</h1>
      </div>
      <div class='comm'>
           <p class='label'> 用户名：</p>
           <input name='userid' type='text' class='input' v-model = 'jobNumber'>
      </div>
      <div class='comm'>
           <p class='label'> 密&#12288码：</p>
           <input name='userpwd' type='password' class='input' v-model = 'password'>
      </div>
      <div class='comm'>
           <input type='submit' value='登录' id='submit' @click = 'login' style='backgroundColor:#99cccc;'>
      </div>
  </div>
</div>
	
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'
export default {
  name: 'login',
  data(){
  	return {
      	jobNumber:'',
  		password:''
  	}
  },
  methods:{
  	...mapMutations([
      'SET_USER',
      'SET_SHOW'
  	]),
    login(){
    	// this.SET_USER({username:'陈明'});
     //      this.$router.push('/layout')
     //       this.SET_SHOW(false)
    	let params = new URLSearchParams();
    	params.set('jobNumber',Number(this.jobNumber));
    	params.set('password',this.password);
    	let this_ = this
      axios({
      	url:'/login',
      	method:'post',
      	headers:{
         'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
      	},
      	data:params
      }).then(function(res){
      	if(res.data.code == 0){
      		 console.log(typeof(res.data.data))
          this_.SET_USER(res.data.data);
          this_.$router.push('/layout')
          this_.SET_SHOW(false)
      	}
      })
    }
  },
  created:function(){
  	window.onload = function(){
		var winWidth = document.documentElement.clientWidth;
		var winHeight = document.documentElement.clientHeight;
		var width = document.getElementById('login').offsetWidth;
		var height = document.getElementById('login').offsetHeight;
		var margin = (winHeight-height)/2 + 20 +'px';
		document.getElementById('login').style.marginTop = margin;
    }
    window.onresize=function(){
		var winWidth = document.documentElement.clientWidth;
		var winHeight = document.documentElement.clientHeight;
		var width = document.getElementById('login').offsetWidth;
		var height = document.getElementById('login').offsetHeight;
		var margin = (winHeight-height)/2 + 20+'px';
		document.getElementById('login').style.marginTop = margin;
    }
  }
}
</script>

<style lang='stylus' scoped>

#wraps
	width:920px;
	height:800px;
    #login
		width:550px;
		height:440px;
		margin:0 auto;
		margin-top:250px;
		border-radius:20px;
		padding:0;
		background-color:#3c3645;
		box-shadow:5px 5px 40px #336699;
		z-index:999;
		opacity:0.7;
		.comm
			width:400px;
			margin:0 auto;
			margin-top:20px;
			height:80px;
			#submit
				width:200px;
				height:60px;
				margin-top:10px;
				border-radius:15px;
				border:0px;
				margin-left:100px;
				font-size:30px;
			    font-weight:bold;
			#submit:hover
				transform:scale(1.2,1.2);
				cursor: pointer;
				transition:all 0.3s;
			.label
				float:left;
				width:90px;
				height:60px;
				margin-top:10px;
				font-size:20px;
				line-height:60px;
				font-weight:bold;
				margin-left:5px;
			.input
				width:300px;
				height:60px;
				margin-top:10px;
				border-radius:15px;
				border:0px;	
		#head 
			img
				width:60px;
				height:60px;
				border-radius:30px;
				margin-top:10px;
				float:left;
				margin-left:80px;
			h1
				margin-left:20px;
				float:left;
				font-size:35px;
				margin-top:16px;
		
	
</style>



